import React , {useState} from 'react'
import GoogleMapReact from 'google-map-react'
import {graphql, useStaticQuery} from 'gatsby'
import { useForm } from "react-hook-form";
import {Modal,Button} from 'react-bootstrap'
const LocationIcon = ({text})=>{
    const data = useStaticQuery(graphql`
	query{
		marker: file(relativePath: {eq: "resource/mapmarker.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }}`)
    
    return(
        <div>
        <img src={data.marker.childImageSharp.fluid.originalImg} alt="mapmarker"/>
        {text}
        </div>
    )
}

const ContactSection = ()=>{
    const { register, handleSubmit, errors } = useForm();
     const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [name,setName]= useState('')
    const onSubmit = data =>{
        if(data.form_name){
            handleShow();
            setName(data.form_name)
        }
    } ;
  console.log(errors);
    return(
        <section className="our-contact pb0 bgc-f7">
            <div className="container">
                <div className="row">
                    <div className="col-lg-7 col-xl-8">
                        <div className="form_grid">
                            <h4 className="mb5">Send Us An Email</h4>
                            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. In gravida quis libero eleifend ornare. Maecenas mattis enim at arcu feugiat, sit amet blandit nisl iaculis. Donec lacus odio, malesuada eu libero sit amet, congue aliquam leo. In hac habitasse platea dictumst.</p>
                            <form onSubmit={handleSubmit(onSubmit)} className="contact_form">
                                <div className="row">
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <input id="form_name" name="form_name" className="form-control" required="required" type="text" placeholder="Name" ref={register({required: true, maxLength: 80})}/>
                                            {errors.form_name && <span>This field is required</span>}
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <input id="form_email" name="form_email" className="form-control required email" required="required" type="email" placeholder="Email" ref={register({required: true, pattern: /^\S+@\S+$/i})}/>
                                            {errors.form_email && <span>This field is required</span>}
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <input id="form_phone" name="form_phone" className="form-control required phone" required="required" type="number" ref={register({required: true, minLength: 6, maxLength: 12})}/>
                                            {errors.form_phone && <span>This field is required</span>}
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <input id="form_subject" name="form_subject" className="form-control required" required="required" type="text" placeholder="Subject" ref={register({required: true, maxLength: 120})}/>
                                            {errors.form_subject && <span>This field is required</span>}
                                        </div>
                                    </div>
                                    <div className="col-sm-12">
                                        <div className="form-group">
                                            <textarea id="form_message" name="form_message" className="form-control required" rows="8" required="required" placeholder="Your Message" ref={register}></textarea>
                                            {errors.form_message && <span>This field is required</span>}
                                        </div>
                                        <div className="form-group mb0">
                                            <button type="submit" className="btn btn-lg btn-thm">Send Message</button>
                                        </div>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                    <Modal show={show} onHide={handleClose}>
                        <Modal.Header closeButton>
                        <Modal.Title>Success</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>Hi {name}, We've received your message. Thanks! </Modal.Body>
                        <Modal.Footer>
                        <Button variant="secondary" onClick={handleClose}>
                            Close
                        </Button>
                        </Modal.Footer>
                    </Modal>
                    <div className="col-lg-5 col-xl-4">
                        <div className="contact_localtion">
                            <h4>Contact Us</h4>
                            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. In gravida quis libero eleifend ornare. habitasse platea dictumst.</p>
                            <div className="content_list">
                                <h5>Address</h5>
                                <p>2301 Ravenswood Rd Madison, <br/>WI 53711</p>
                            </div>
                            <div className="content_list">
                                <h5>Phone</h5>
                                <p>(315) 905-2321</p>
                            </div>
                            <div className="content_list">
                                <h5>Mail</h5>
                                <p>info@findhouse.com</p>
                            </div>
                            <div className="content_list">
                                <h5>Skype</h5>
                                <p>findhouse.com</p>
                            </div>
                            <h5>Follow Us</h5>
                            <ul className="contact_form_social_area">
                                <li className="list-inline-item"><a href="#"><i className="fa fa-facebook"></i></a></li>
                                <li className="list-inline-item"><a href="#"><i className="fa fa-twitter"></i></a></li>
                                <li className="list-inline-item"><a href="#"><i className="fa fa-instagram"></i></a></li>
                                <li className="list-inline-item"><a href="#"><i className="fa fa-google"></i></a></li>
                                <li className="list-inline-item"><a href="#"><i className="fa fa-pinterest"></i></a></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div className="container-fluid p0 mt50">
                <div className="row">
                    <div className="col-lg-12">
                        <div className="h600" >
                        <GoogleMapReact
						bootstrapURLKeys={{ key: 'AIzaSyAAz77U5XQuEME6TpftaMdX0bBelQxXRlM' }}
						defaultCenter={{
										lat: 51.510280,
										lng: -0.084028
										}}
						defaultZoom={14}
						>
                        <LocationIcon
                            lat={51.510280}
                            lng={-0.084028}
                            text="Awesome Place"
                        />
        			</GoogleMapReact>
                        </div>
                    </div>
                </div>
            </div>
	</section>
    )
}
 export default ContactSection